'use client';

import { ChevronRight } from 'lucide-react';
import Image from 'next/image';
import styles from './MovieRow.module.css';

interface Movie {
    id: number;
    title: string;
    image: string; // URL
    isNew?: boolean;
}

// Mock Data
const MOCK_MOVIES: Movie[] = [
    { id: 1, title: 'Avatar: Fogo e Cinzas', image: 'https://images.unsplash.com/photo-1536440136628-849c177e76a1?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 2, title: 'Truque de Mestre 3', image: 'https://images.unsplash.com/photo-1626814026160-2237a95fc5a0?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 3, title: 'SISU', image: 'https://images.unsplash.com/photo-1595769816263-9b910be24d5f?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 4, title: 'Zootopia 2', image: 'https://images.unsplash.com/photo-1608889476561-6242cfdbf622?auto=format&fit=crop&w=300&q=80' },
    { id: 5, title: 'Five Nights at Freddys 2', image: 'https://images.unsplash.com/photo-1509347528160-9a9e33742cd4?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 6, title: 'O Brilho do Diamante', image: 'https://images.unsplash.com/photo-1510511459019-5dda7724fd87?auto=format&fit=crop&w=300&q=80' },
    { id: 7, title: 'Example Movie', image: 'https://images.unsplash.com/photo-1485846234645-a62644f84728?auto=format&fit=crop&w=300&q=80' },
];

export default function MovieRow({ title }: { title: string }) {
    return (
        <div className={styles.row}>
            <div className={styles.header}>
                <h2 className={styles.title}>{title}</h2>
                <button className={styles.viewAll}>
                    Ver todos <ChevronRight size={16} />
                </button>
            </div>

            <div className={styles.listContainer}>
                {MOCK_MOVIES.map((movie) => (
                    <div key={movie.id} className={styles.card}>
                        <div className={styles.imageWrapper}>
                            {/* Image */}
                            <Image
                                src={movie.image}
                                alt={movie.title}
                                fill
                                className={styles.cardImage}
                                unoptimized
                            />

                            {/* Overlay on Hover */}
                            <div className={styles.overlay}>
                                <span className={styles.cardTitle}>{movie.title}</span>
                                <span className={styles.cardMeta}>2024 • 2h 10m</span>
                            </div>
                        </div>

                        {movie.isNew && (
                            <div className={styles.newBadge}>
                                NOVO
                            </div>
                        )}

                        <p className={styles.cardTitleStatic}>{movie.title}</p>
                    </div>
                ))}
            </div>
        </div>
    );
}
